@import "../../../../../assets/mixins/mixins.scss";

.container-2 {
  display: flex;
  height: 650px;
  background-color: #fff;
  &__aside-left {
    width: 50%;
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__aside-right {
    width: 50%;
    padding: 180px 90px;
  }
  &__wrapper {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    @include font();
    color: #000;
    font-size: 38px;
    font-weight: 600;
  }
  &__caption {
    margin-top: 20px;
    font-family: "Dancing Script", cursive;
    font-size: 24px;
    font-weight: 600;
  }
  &__text {
    margin-top: 60px;
    line-height: 25px;
    font-stretch: ultra-condensed;
    letter-spacing: -1px;
    font-size: 18px;
  }
  &__btn {
    @include btn-black();
    margin-top: 60px;
  }
}
@media screen and(max-width:574px) {
  .container-2__aside-right {
    padding: 70px 20px;
  }
  .container-2__wrapper {
    width: 100%;
  }
}
