@import "../../../assets/mixins/mixins.scss";
.container-top {
  height: 450px;
  display: flex;
  flex-direction: column;

  align-items: center;
  &__caption {
    @include font();
    @include font-caption();
    margin: 100px 0 40px 0;
  }
  &__title {
    @include font-title();
  }
}
@media screen and (max-width: 574px) {
  .container-top__title {
    font-size: 40px;
  }
  .container-top__caption {
    font-size: 25px;
  }
}
