@import "../../../../../assets/mixins/mixins.scss";

.parallax {
  height: 780px;
  position: relative;
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  @include flex();
  flex-direction: column;
  &__caption {
    color: #fff;
    font-size: 38px;
  }
  &__title {
    color: #fff;
    font-size: 115px;
    padding: 20px;
    letter-spacing: 10px;
  }
  &__btn {
    @include btn();
  }
}
@media screen and(max-width:574px) {
  .parallax {
    background-size: 200%;
    &__title {
      font-size: 50px;
      text-align: center;
    }
  }
}
