@import "../../../../assets/mixins/mixins.scss";
$borderColor: 1px solid lightgrey;

.aside-right {
  width: 15%;
  border-left: $borderColor;
  display: flex;
  flex-direction: column;
  &__header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-bottom: $borderColor;
  }
  &__text {
    @include font();
    color: rgba(41, 35, 37, 0.8);
  }
  &__cart {
    display: flex;
    flex-direction: column;
    min-height: 200px;
    max-height: 300px;
    overflow-y: auto;
    overflow-x: hidden;
    &-svg {
      fill: lightgray;
      display: block;
      margin: 30px auto;
    }
    &-caption {
      width: 80%;
      margin: 20px auto;
      color: lightgray;
    }
    &-item {
      width: 100%;
      height: 30%;
      display: flex;
      justify-content: space-between;
      padding: 20px 25px;
      position: relative;
      transition: background-color 0.2s;
      &-price {
        z-index: 2;
        overflow: hidden;
        position: absolute;
        top: 20px;
        right: 10px;
        padding-left: 15px;
        transition: all 0.2s;
        background-color: #fff;
      }
      &:hover {
        background-color: lightgray;
      }

      &:hover .aside-right__cart-btns {
        transform: translateX(-40px);
        opacity: 1;
        visibility: visible;
      }
      &:hover .aside-right__cart-item-price {
        transform: translateX(-40px);
        background-color: lightgray;
      }
    }
    &-btns {
      position: absolute;
      display: flex;
      flex-direction: column;
      top: 5px;
      right: -20px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.2s;
      padding-left: 20px;
      background-color: lightgray;
    }
    &-btn {
      background-color: transparent;
      color: #000;

      border-radius: 50%;
      font-size: 20px;
      padding: 0 5px;
      &:hover {
        background-color: grey;
      }
    }
  }
  &__divider {
    width: 90%;
    margin: 10px auto 0 auto;
  }
  &__form {
    padding: 20px;
    &-input {
      resize: none;
      border: $borderColor;
      @include font();
      color: #000;
      box-shadow: 0 0 2px 0 black;
    }
    &-btn-request {
      font-size: 18px;
      cursor: pointer;
    }
    &-caption {
      margin: 10px 0;
    }
    &-btn-submit {
      @include btn-black();
      background: #000;
      color: #fff;
      height: 50px;
      max-width: 100%;
      margin-top: 20px;
    }
  }
}
@media screen and(max-width: 574px) {
  .aside-right {
    width: 100%;
    &__header {
      justify-content: center;
    }
    &__text {
      padding: 0 20px;
      text-align: center;
    }
    &__cart-svg {
      display: block;
      margin: 30px auto;
    }
  }
}
