@import "../../../../../assets/mixins/mixins.scss";

.container-text {
  @include flex();
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  margin: auto;
  text-align: center;
  padding: 80px 0;
  &__title {
    @include font();
    @include font-caption();
    color: #000;
  }
  &__caption {
    font-family: "Dancing Script", cursive;
    font-size: 24px;
    margin: 20px;
  }
  &__paragraph {
    margin-top: 35px;
    @include font();
    color: #000;
    font-weight: 500;
  }
}
