.nav-menu {
  width: 100%;
  height: 40px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  z-index: 101;
  display: none;
}
.nav-menu__btn {
  display: block;
  padding: 0;
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  background: transparent;
  width: 25px;
  height: 25px;
  transition: all 0.1s;
  &::after {
    content: "";
    width: 100%;
    height: 2px;
    background: #000;
    position: absolute;
    bottom: 33.333%;
    left: 0;
  }
  &::before {
    content: "";
    width: 100%;
    height: 2px;
    background: #000;
    position: absolute;
    top: 33.333%;
    left: 0;
  }
  &_active {
    &::after {
      top: #{calc(50% - 4px)};
      transform: rotate(-45deg);
      transition: all 0.1s;
    }
    &::before {
      bottom: #{calc(50%)};
      transform: rotate(45deg);
      transition: all 0.1s;
    }
  }
}
@media screen and (max-width: 574px) {
  .nav-menu {
    display: block;
  }
}
