@import "../../../../../assets/mixins/mixins.scss";

.container-1 {
  height: 780px;
  background-color: #f9db79;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #fff;
  background-image: url("../../../../../assets/images/home/1.png");
  background-repeat: no-repeat;
  background-position: 49.5% 50%;
  background-size: 15%;
  &__title-main {
    @include font-title();
    margin: 30px auto;
  }
  &__title-secondary {
    @include font();
    @include font-caption();
    margin-top: 300px;
  }
  &__btn {
    @include btn();
  }
}
@media screen and(max-width:574px) {
  .container-1 {
    background-size: contain;
    &__title-main {
      font-size: 50px;
      text-align: center;
    }
  }
}
