.social {
  position: fixed;
  top: 75px;
  right: 165px;
  z-index: 100;
  display: flex;
  justify-content: flex-end;
  transition: all 0.2s ease-in;
  &__link {
    display: flex;
  }
  &__img {
    width: 30px;
    height: 30px;
  }
}
.social.social_active {
  transform: translateY(0);
}
@media screen and(max-width: 574px) {
  .social {
    transform: translateY(-250%);
    right: auto;
    left: 30px;
  }
}
