@import "../../assets/mixins/mixins.scss";
.header {
  width: 100%;
  height: 50px;
  background-color: #242323;
  @include flex();
  @include font();
  font-size: 16px;
  letter-spacing: 0.5px;
}
@media screen and (max-width: 574px) {
  .header {
    margin-top: 40px;
    height: 100px;
    padding: 15px;
    text-align: center;
  }
}
