@import "../../../assets/mixins/mixins.scss";
.book {
  display: flex;
  flex-direction: column;
  &__wrapper {
    width: 100%;
    max-width: 900px;
    margin: 0 auto 80px auto;
    height: 300px;
    background-color: #ffbfb3;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 0;
  }
  &__title {
    @include font();
    color: #000;
  }
  &__text {
    @include font();
    color: #000;
    margin-top: 25px;
    font-weight: 500;
  }
  &__btn {
    @include btn();
    background-color: #000;
    border: 2px solid #000;
    margin-top: 30px;

    &:hover {
      opacity: 0.8;
    }
  }
}
