@import "../../../../../assets/mixins/mixins.scss";

.parallax2 {
  height: 725px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  &__img {
    width: 100%;
    object-fit: contain;
  }
}
@media screen and(max-width:574px) {
  .parallax2 {
    height: 500px;
    &__img {
      height: 120%;
      object-fit: cover;
    }
  }
}
