@import "../../../../assets/mixins/mixins.scss";

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    @include font();
    @include font-caption();
    color: #000;
    padding-bottom: 30px;
  }
  &__divider {
    width: 15%;
  }
  &__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &-item {
      width: 30%;
      padding: 10px;
      text-align: center;

      cursor: pointer;
      &-img {
        width: 60%;
        transition: all 0.2s ease-in;
      }
      &-title {
        @include font();
        font-size: 22px;
        color: #000;
        transition: all 0.2s ease-in;
        padding: 20px 0;
      }
      &-text {
        @include font();
        color: #000;
        font-weight: 500;
      }
      &:hover &-img,
      &:hover &-title {
        transform: scale(1.1);
      }
    }
  }
}
@media screen and(max-width: 574px) {
  .menu__grid-item {
    width: 100%;
  }
}
