@import "../../assets/mixins/mixins.scss";
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 4px solid var(--main-color_3);
  -webkit-text-fill-color: #fff;
  transition: background-color 5000s ease-in-out 0s;
}
.footer {
  width: 100%;
  height: 355px;
  background-color: #000;

  &__content {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    padding-top: 70px;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    &-container {
      flex: 0 0 20%;
      display: flex;
      flex-direction: column;
      &:last-of-type {
        flex: 0 0 40%;
      }
      &:nth-child(2) {
        padding-left: 10px;
      }
    }
    &-title {
      font-stretch: ultra-condensed;
      letter-spacing: 0.5px;
      font-size: 24px;
      font-weight: 600;
      padding-bottom: 18px;
      color: #fff;
    }
    &-text {
      font-stretch: ultra-condensed;
      letter-spacing: 0.5px;
      font-size: 16px;
      font-weight: 600;
      color: #fff;
    }
  }
  &__form {
    &-input {
      width: 100%;
      border: 2px solid #fff;
      background: transparent;
      @include font();
      padding: 5px 21px;
      transition: 0.2s ease-in;
      &:hover {
        border: 4px solid #fff;
      }
      &:focus {
        border: 4px solid red;
      }
      &:visited {
        border: 4px solid red;
      }
    }
    &-btn {
      margin-top: 15px;
      @include btn_footer();
    }
  }
  &__caption {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    &-link {
      color: #fff;
      text-decoration: underline;
      transition: 0.2s ease-in;
      cursor: pointer;
      &:hover {
        color: var(--main-color_2);
      }
    }
  }
}
@media screen and(max-width: 574px) {
  .footer {
    height: auto;
    &__info {
      flex-direction: column;
      &-container {
        flex: 0 0 100%;
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        align-items: center;
        &:last-of-type {
          flex: 0 0 100%;
        }
        &:nth-child(2) {
          padding-left: 0px;
        }
      }
    }
    &__caption {
      flex-direction: column;
      align-items: center;
    }
  }
}
