@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap");
:root {
  --main-color_1: #2f2e2e;
  --main-color_2: #706d6d;
  --main-color_3: #292a2d;
}
@mixin flex() {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin font() {
  line-height: 25px;
  font-stretch: ultra-condensed;
  letter-spacing: -1px;
  font-size: 18px;
  color: #fff;
  font-weight: 700;
}
@mixin font-title() {
  color: #fff;
  font-size: 115px;
  letter-spacing: 10px;
}
@mixin font-caption() {
  color: #fff;
  font-size: 38px;
}
@mixin btn() {
  width: 100%;
  border: 2px solid #fff;
  background: transparent;
  @include font();
  font-size: 24px;
  font-weight: 600;
  max-width: 140px;
  text-align: center;
  transition: 0.3s ease-in;
  cursor: pointer;
  padding: 5px 0px;
  &:hover {
    background-color: var(--main-color_3);
    border: 2px solid var(--main-color_3);
  }
}
@mixin btn-black() {
  width: 100%;
  border: 2px solid #000;
  background: transparent;
  @include font();
  font-size: 24px;
  font-weight: 600;
  max-width: 130px;
  text-align: center;
  transition: 0.3s ease-in;
  cursor: pointer;
  padding: 5px 0;
  color: #000;
  &:hover {
    background-color: var(--main-color_3);
    border: 2px solid var(--main-color_3);
    color: #fff;
  }
}
@mixin btn_footer() {
  width: 100%;
  border: 2px solid #fff;
  background: transparent;
  @include font();
  font-size: 24px;
  font-weight: 500;
  transition: 0.3s ease-in;
  cursor: pointer;
  padding: 5px 0;
  &:hover {
    background-color: var(--main-color_3);
    border: 2px solid var(--main-color_3);
  }
}
