@import "../../../../assets/mixins/mixins.scss";

.aside-left {
  width: 15%;
  border-right: 1px solid lightgray;
  &__accordion-summary {
    @include font();
    color: #000;
    font-weight: 500;
    letter-spacing: 1px;
  }
  &__accordion-content {
    display: flex;
    flex-direction: column;
  }
  &__btn-option {
    padding: 13px 36px;
    font-size: 14px;
    color: rgb(41, 35, 37);
    border-left: 3px solid transparent;
    &:hover {
      background-color: rgb(234, 233, 234);
    }
    &_selected {
      background-color: rgb(234, 233, 234);
      border-left: 3px solid #635e60;
    }
  }
}
@media screen and(max-width: 574px) {
  .aside-left {
    width: 100%;
  }
}
